 const priceSlider = (value) => {
        document.getElementById("amount").value = "$" + value[0];
        document.getElementById("amount2").value = "$" + value[1];
    }
function ContextAwareToggle({ children, eventKey, callback }) {
	const currentEventKey = useContext(AccordionContext);
  
	const decoratedOnClick = useAccordionToggle(
	  eventKey,
	  () => callback && callback(eventKey),
	);
  
	const isCurrentEventKey = currentEventKey === eventKey;
  
	return (
		<Card.Header 
			onClick={decoratedOnClick}
			className={isCurrentEventKey ? 'active' : ''}
			>
		<div className="mb-0">
			<div className="btn btn-link text-left" role="button">{children}</div>
		</div>
	</Card.Header>
	);
  }
  
const ProgressBar = (props) => {
  const { bgcolor, completed } = props;
  const [complete , setComplete] = useState(0);
    useEffect(() => {
		document.addEventListener("scroll", () => {
			console.log(window.scrollY)
            if (window.scrollY > 1780) {
				setInterval(() => setComplete(completed), 1000);
            }
})},[]);
	const containerStyles = {
    width: '100%',
    backgroundColor: "#e0e0de",
    borderRadius: 50,
  }
  const fillerStyles = {
    height: '100%',
    width: `${complete}%`,
    backgroundColor: bgcolor,
    borderRadius: 'inherit',
    textAlign: 'right',
    transition: 'width 1s ease-in-out',
  }



  return (
    <div style={containerStyles} className="bar-container">
      <div style={fillerStyles}>
      </div>
    </div>
  );
};

const Completionist = () => <span>You are good to go!</span>;

// Renderer callback with condition
const renderer = ({days, hours, minutes, seconds, completed }) => {
  if (completed) {
    // Render a complete state
    return <Completionist />
  } else {
    // Render a countdown
    return (
      <ul>
	  	<li>Days<span id="days">{days}</span></li>
		<li>Hours<span id="hours">{hours}</span></li>
		<li>Minutes<span id="minutes">{minutes}</span></li>
		<li>Seconds<span id="seconds">{seconds}</span></li>
     </ul>
    );
  }
};
